.delivery {
    font-family: 'Open Sans', sans-serif;
    padding: 40px 0;
    color: #444;
}
.delivery__head {
    font-size: 33px;
    font-weight: 500;
    text-align: center;
}
.delivery__text {
    font-size: 16px;
    color: #333;
    margin-bottom: 15px;
    display: block;
}
.delivery__table {
    border: 1px solid #ddd;
    border-collapse: collapse;
    color: #666;
    margin-bottom: 30px;
}
.delivery__table td {
    border: 1px solid #ddd;
    padding: 8px;
    font-size: 13px;
    line-height: 1.4;
}
.delivery__text--small {
    font-size: 14px;
    color: #666;
    margin-bottom: 15px;
}


