.navigation {
    position: relative;
    z-index: 99;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #fff;
    -webkit-box-shadow: 0px 10px 24px 0px rgba(51, 51, 51, 0.25);
    box-shadow: 0px 10px 24px 0px rgba(51, 51, 51, 0.25);
}

.navigation__top {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    height: 60px;
}

.navigation.navigation--small .navigation__bottom {
    height: 60px;
}

.navigation__bottom {
    position: relative;
}

.logo{
    position: relative;
    min-width: 33.3%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: center;
    background-color: #a40050;
    color: #fff;
    text-decoration: none;
    padding: 5px 10px;
}
.svg-icon-logo {
    width: 100%;
    height: 100%;
}

.navigation__top-left {
    position: relative;
    -ms-flex: 1 1 60%;
    flex: 1 1 60%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.navigation__schedule, .navigation__phone {
    -ms-flex: 1 1 50%;
    flex: 1 1 50%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
    text-align: center;
    padding: 8px;
    background-color: #e4e4e4;
    border-right: 1px solid #e1e1e1;
}

.navigation__schedule .navigation__phone-tel, .navigation__phone .navigation__phone-tel {
    font-size: 18px;
    color: #333;
    text-decoration: none;
}

.navigation__schedule .navigation__phone-callback, .navigation__phone .navigation__phone-callback {
    font-size: 12px;
    color: #064a69;
    font-family: ProximaNovaSemibold;
    cursor: pointer;
}
.navigation__phone-callback:hover {
    border-bottom: 1px solid #064a69;
}

.navigation__schedule > span, .navigation__phone > span {
    color: #868686;
    font-size: 12px;
}

.navigation__schedule > strong {
    font-size: 21px;
}

.navigation__top-right{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex: 1 1 40%;
    flex: 1 1 40%;
    text-align: center;
    height: 100%;
}

.toggle-menu__link{
    position: relative;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    height: 100%;
    color: #fff;
    line-height: 60px;
    font-weight: 300;
    background-color: #a40050;
    text-decoration: none;
    z-index: 22;
    -webkit-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
}
.toggle-menu__link:hover {
    opacity: .7;
}

.nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.nav__link {
    -ms-flex: 1 1 20%;
    flex: 1 1 20%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    height: 120px;
    text-align: center;
    font-size: 12px;
    font-family: ProximaNovaBold;
    color: #a50050;
    text-decoration: none;
    text-transform: uppercase;
    border-right: 1px solid #e4e4e4;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}
.nav__link .nav__link-text {
    font-weight: 700;
}
.nav__link:hover, .nav__link.active {
    background-color: #F4F4F4;
    color: #064a69;
}
.navigation .nav__link.active .svg-icon {
    fill: #064a69;
}
.navigation.navigation--small .nav__link {
    -ms-flex-direction: row;
    flex-direction: row;
    height: 60px;
}
.navigation .nav__link .svg-icon {
    fill: #a50050;
    height: 32px;
    width: auto;
    max-width: 80px;
    margin-right: 12px;
    margin-bottom: 0;
}
.navigation .nav__link .svg-icon.svg-icon-2x {
    width: 50px;
}
.navigation .nav__link .svg-icon.svg-icon-3x {
    width: 70px;
}
.navigation .nav__link .svg-icon.svg-icon-anglechilr {
    width: 77px;
}
.navigation .nav__link .svg-icon.svg-icon-armchair {
    width: 34px;
}
.navigation .nav__link .svg-icon.svg-icon-chair {
    width: 26px;
}


