.about-store {
    padding: 40px 0;
}
.about-store__head {
    font-size: 33px;
    text-align: center;
    font-weight: 500;
    color: #444;
    margin-bottom: 20px;
}
.about-store__desc {
    text-align: center;
    max-width: 900px;
    margin: 0 auto 20px;
    color: #666;
    font-size: 16px;
    line-height: 1.5;
}
.about-store__wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding-bottom: 40px;
}
.about-store__item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-pack: center;
    justify-content: center;
    text-align: center;
    -ms-flex: 1 1 33%;
    flex: 1 1 33%;
    border-right: 1px solid #e4e4e4;
    padding: 10px 15px;
}
.about-store__item .svg-icon {
    fill: #a50050;
    font-size: 40px;
    margin-bottom: 20px;
}
.about-store__item-title {
    color: #a50050;
    text-decoration: none;
    font-size: 20px;
}
.about-store__item-title span {
    font-weight: 400;
    display: block;
    font-size: 14px;
}
.about-store__item:last-child {
    border-right: none;
}

.blockmap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding-bottom: 30px;
}

.blockmap_left, .blockmap_right {
    width: 49%;
    height: 500px;
}


