.self-selection {
    padding: 40px 0;
}
.self-selection .title-accent {
    font-size: 24px;
}
.self-selection__wrap {
    display: -ms-flexbox;
    display: flex;
}
.self-selection__left {
    -ms-flex: 1 1 40%;
    flex: 1 1 40%;
}
.self-selection__text {
    max-width: 320px;
    margin-bottom: 20px;
}
.self-selection__img {
    padding-right: 40px;
}
.self-selection__img img {
    max-width: 100%;
}
.self-selection__center {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    font-size: 14px;
    color: #bdbdbd;
    text-transform: uppercase;
}
.self-selection__center span {
    padding: 10px 0;
}
.self-selection__center::before, .self-selection__center::after {
    position: absolute;
    content: "";
    background-color: #bdbdbd;
    width: 1px;
    height: calc(50% - 20px);
    left: 50%;
}
.self-selection__center::before {
    bottom: 0;
}
.self-selection__center::after {
    top: 0;
}
.self-selection__right {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    -ms-flex-item-align: center;
    align-self: center;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex: 1 1 40%;
    flex: 1 1 40%;
}


