body.popup-open {
    overflow: hidden;
}

.review-line {
    border-bottom: 1px solid #e4e4e4;
    padding: 40px 0;
}
.review-line__head {
    margin-bottom: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: start;
    align-items: flex-start;
}
.review-line__head  .title-accent {
    font-size: 24px;
    margin-right: 20px;
}
.review-line__title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
}
.review-line__discount {
    position: relative;
    background-color: #A50050;
    padding: 8px 10px;
    border-radius: 3px;
    color: #fff;
    font-size: 12px;
    line-height: 1.2;
    max-width: 430px;
    margin-right: 10px;
    text-transform: uppercase;
}
.review-line__discount::after {
    content: "";
    display: block;
    position: absolute;
    width: 12px;
    height: 12px;
    background-color: #a50050;
    right: -6px;
    top: 50%;
    margin-top: -6px;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}
.review-line__discount span {
    font-family: ProximaNovaBold;
}
.review-grid {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
}
.review-grid .popup-link {
    width: 33%;
    cursor: pointer;
    padding: 5px;
    background-color: #fff;
}
.review-item__img img {
    max-width: 100%;
}
.review-item__content {
    padding: 10px 10px 20px;
}
.review-item__name {
    margin: 5px 0 10px;
    font-weight: 600;
}
.review-item__rating {
    margin-bottom: 15px;
}

.popup-review {
    max-width: 1500px;
    width: calc(100vw - 30px);
    background-color: transparent;
}
.popup-review .popup-close {
    top: -15px;
    right: -15px;
    color: #fff;
}
.popup-review__wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-align: start;
    align-items: flex-start;
}
.popup-review__img {
    padding: 15px;
    background-color: #fff;
    margin: 10px;
    -ms-flex: 1 1 70%;
    flex: 1 1 70%;
}
.popup-review__content {
    padding: 15px;
    background-color: #fff;
    margin: 10px;
    -ms-flex: 1 1 30%;
    flex: 1 1 30%;
    max-width: 400px;
}
.popup-review__name {
    font-weight: 600;
    margin-bottom: 10px;
    font-size: 18px;
}
.popup-review .stars-line {
    margin-bottom: 15px;
}
.form-review .form-wrap .label-wrap {
    width: 100%;
    max-width: 100%;
}
.form-review .form-wrap .label-text textarea {
    height: 160px;
}
.form-review .form-wrap .label-text {
    margin-bottom: 20px;
}


