.callback-line {
    position: relative;
    padding: 40px 0 60px;
}
.callback-line .title-accent {
    font-size: 24px;
}
.callback-line__wrap {
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 30px;
}
.callback-line__text, .callback-line__form {
    -ms-flex: 1 1 40%;
    flex: 1 1 40%;
}
.callback-line__form {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 100%;
    -ms-flex-item-align: center;
    align-self: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.callback-line__form .label-wrap {
    max-width: 100%;
}
.callback-line__form .textarea {
    border: none;
    width: 100%;
    font-size: 16px;
    background-color: transparent;
    resize: vertical;
    height: 90px;
}
.callback-line__form .btn {
    margin-top: 20px;
}
.callback-line__text p {
    font-size: 16px;
    line-height: 1.5;
    margin: 0 0 20px;
}

.director {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
}
.director__img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    background-color: #ff9c44;
    overflow: hidden;
}
.director__img img {
    max-width: 100%;
}
.director__content {
    margin-left: 20px;
}
.director__name {
    font-family: ProximaNovaExtrabold;
    font-size: 16px;
    line-height: 1.5;
    text-transform: uppercase;
}
.director__post {
    font-size: 12px;
    color: #868686;
    text-transform: uppercase;
}
.director__text {
    margin-top: 15px;
    font-size: 16px;
    line-height: 1.5;
    font-family: ProximaNovaLightItalic;
}
.director__text i {
    font-family: ProximaNovaBoldItalic;
}


