.location-line {
    position: relative;
    padding: 40px 0;
    border-bottom: 1px solid #e4e4e4
}
.location-line .title-accent {
    font-size: 24px;
}

.location-line__wrap {
    display: -ms-flexbox;
    display: flex;
    padding-top: 40px;
    padding-bottom: 40px;
    -ms-flex-pack: justify;
    justify-content: space-between;
}
.location-slider__wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}
.location-line__wrap .location-line__map, .location-slider__wrap {
    width: 50%;
    padding: 0 15px;
    min-height: 350px;
    max-height: 550px;
    -ms-flex: 1 1 auto;
    flex: 1 1 50%;
}

.location-slider .location-slider__slide {
    width: 100%;
    padding: 5px;
    opacity: 0.5;
    -webkit-transition: 0.2s ease-in-out;
    -o-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
    cursor: pointer;
}
.location-slider .location-slider__slide:hover {
    opacity: 0.8;
}
.location-slider .location-slider__slide.slick-current {
    opacity: 1;
}
.location-slider img, .location-slider__image img {
    width: 100%;
}


